<template>
<div>
  <div class="top_sreach">
    <div class="top_zi">学生姓名:</div>
    <div class="search_input">
      <input type="text" class="input_box" v-model="studentForm.name" @blur="getStudent()">
    </div>
    <div class="top_zi">所在班级:</div>
    <div class="search_input">
      <input type="text" class="input_box" v-model="studentForm.classid" @blur="getStudent()">
    </div>
    <div class="top_zi">学生状态:</div>
    <div class="search_input">
      <el-select v-model="studentForm.feeling">
      <el-option
        v-for="item in options"
        :key="item.value"
        :label="item.label"
        :value="item.value">
      </el-option>
    </el-select>
    </div>
    <div class="search_input">
      <el-button type="success" round><i class="el-icon-s-promotion" @click="getStudent()"></i>搜索</el-button>
    </div>
    <div class="search_input">
      <el-button type="warning" round><i class="el-icon-refresh" @click="resetSearch()"></i>重置</el-button>
    </div>
  </div>
  <div class="top_main">
     <el-table
    :data="studentList"
    stripe
    style="width: 100%">
    <el-table-column
      prop="name"
      label="姓名"
      >
    </el-table-column>
    <el-table-column
      prop="sex"
      label="姓别"
      >
    </el-table-column>
    <el-table-column
      prop="class_id"
      label="班级"
      >
    </el-table-column>
    <el-table-column
      prop="familyPhone"
      label="手机号">
    </el-table-column>
    <el-table-column
      prop="feeling"
      label="状态">
    </el-table-column>
    <el-table-column
      label="心情"
      width="300"
      >
      <template slot-scope="scope">
        <el-button type="danger" v-for="item in scope.row.mood" :key="item" size="mini">{{item}}</el-button>
      </template>
    </el-table-column>
    <el-table-column label="操作" width="180px">
    <template slot-scope="scope">
        <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.id)"></el-button>
        <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeUserById(scope.row.id)"></el-button>
        <el-tooltip effect="dark" content="详情" placement="top" :enterable="false" >
        <el-button type="warning" icon="el-icon-setting" size="mini" @click="setRoles(scope.row)"></el-button>
    </el-tooltip>
              </template>
          </el-table-column>
  </el-table>
   <div class="block">
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="studentForm.pagenum"
      :page-sizes="[1, 2, 5, 10]"
      :page-size="studentForm.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
  </div>
  </div>
</div>
</template>
<script>
export default {
  data () {
    return {
      options: [
        {
          value: '异常',
          label: '异常',
          key: 0
        },
        {
          value: '正常',
          label: '正常',
          key: 1
        },
        {
          value: '',
          label: '所有',
          key: 2
        }
      ],
      studentForm: {
        name: '',
        classid: '',
        feeling: '',
        pagenum: 1,
        pagesize: 5
      },
      studentList: [],
      total: 0
    }
  },
  created () {
    this.getStudent()
  },
  methods: {
    async getStudent () {
      const { data: res } = await this.$http.get('student/proclassandname', { params: this.studentForm })
      if (res.meta.status !== 200) {
        return this.$message.error('获取失败')
      }
      if (res.meta.msg === '无查询到数据') {
        this.studentList = []
        this.total = 0
        return this.$message.error('无查询到数据')
      }
      this.studentList = res.data.records
      this.total = res.data.total
    },
    handleSizeChange (newSize) {
      this.studentForm.pagesize = newSize
      this.getStudent()
    },
    handleCurrentChange (newPage) {
      this.studentForm.pagenum = newPage
      this.getStudent()
    },
    resetSearch () {
      this.studentForm.name = ''
      this.studentForm.classid = ''
      this.studentForm.name = ''
      this.studentForm.classid = ''
      this.getStudent()
    }
  }
}
</script>
<style lang="less" scoped>
.top_sreach{
  width: 100%;
  height: 60px;
  background-color: white;
  border-radius: 10px;
}
.search_input{
  line-height: 60px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 15px;
  float: left;
  margin-left: 10px;
}
.top_zi{
  line-height: 60px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 15px;
  margin-left: 20px;
  float: left;
  color: #6b6b6b;
}
.input_box{
  width: 180px;
  height: 25px;
  border-radius: 5px;
  border: 1px solid #b6b6b6;
}
.top_main{
  width: 100%;
  margin-top: 10px;
  background-color: white;
  border-radius: 10px;
}
</style>
