<template>
<el-container class="home-container">
  <el-header>
    <div>
      <!-- <img src="../assets/logo.png" alt=""> -->
      <span class="logo">学生心理管理系统</span>
      <span class="shou" @click="toggleCollapse">≡</span>
      <span class="topmenu" @click="changePath('important')" id="important"><i class="el-icon-view"></i>重点关注</span>
      <span class="topmenu" @click="changePath('jiankong')" id="jiankong"><i class="el-icon-video-camera-solid"></i>监控画面</span>
      <span class="topmenu" @click="changePath('qingxu')" id="qingxu"><i class="el-icon-timer"></i>实时情绪</span>
      <span class="topmenu" @click="changePath('xitong')" id="xitong"><i class="el-icon-setting" ></i>系统管理</span>
    </div>
    <!-- <el-button type="info" @click="logout">
      退出
    </el-button> -->
    <div>
      <el-avatar icon="el-icon-user-solid" :size="30" style="margin-right: 10px"></el-avatar>
      <p class="user">admin</p>
    </div>
  </el-header>
  <el-container class="pp">
    <el-aside :width="isCollapse ? '64px' :'200px'">
      <div class="asideTop">
        <div class="asideTopLeft">
          <el-avatar icon="el-icon-user-solid" class="touxiang" :size="60"></el-avatar>
        </div>
        <div class="asideTopRight">
          <div>
            <p style="margin-left: 5px">admin</p>
          </div>
          <div>
            <div class="zaixian" style="float:left;margin-top:5px;margin-left:-5px"></div><div style="float:left;margin-left:5px">在线</div>
          </div>
        </div>
         <div style="margin-left:5px" class="zhuxiao">
            <p @click="logout">注销</p>
          </div>
      </div>
      <!-- <div class="toggle-button" @click="toggleCollapse"> ||| </div> -->
      <el-menu class="el-menu-vertical-demo" background-color="#0f2a50" text-color="#fff" active-text-color="#409eff" :unique-opened="true" :collapse="isCollapse" :collapse-transition="false" :router="true">
        <div v-for="(item,i) in menuList" :key="i" >
        <el-menu-item  v-if="item.active === activePath" :index="item.index" @click="addTab(item.index)">
            <i :class="item.icon"></i>
            <span slot="title">{{item.name}}</span>
        </el-menu-item>
        </div>
      </el-menu>
    </el-aside>
    <!-- <el-scrollbar style="width:110%;height: 20px;"></el-scrollbar> -->
    <el-main>
      <el-tabs v-model="editableTabsValue" type="card" closable @tab-remove="removeTab">
        <el-tab-pane
        v-for="(item) in editableTabs"
        :key="item.name"
        :label="item.title"
        :name="item.name"
        :tab-click="change()"
        >
        </el-tab-pane>
      </el-tabs>
      <router-view />
    </el-main>
  </el-container>
</el-container>
</template>

<script>
import $ from 'jquery'
export default {
  data () {
    return {
      isCollapse: false,
      activePath: '',
      menuList: [
        {
          index: '/student',
          name: '学生管理',
          active: 'xitong',
          icon: 'el-icon-user'
        },
        {
          index: '/teacher',
          name: '教师管理',
          active: 'xitong',
          icon: 'el-icon-s-custom'
        },
        {
          index: '/class',
          name: '班级管理',
          active: 'xitong',
          icon: 'el-icon-s-management'
        },
        {
          index: '/problemStudent',
          name: '问题学生',
          active: 'important',
          icon: 'el-icon-loading'
        },
        {
          index: '/psyHealth',
          name: '心理健康',
          active: 'important',
          icon: 'el-icon-s-opportunity'
        },
        {
          index: '/solveReport',
          name: '解决报告',
          active: 'important',
          icon: 'el-icon-notebook-1'
        },
        {
          index: '/jiankong',
          name: '监控画面',
          active: 'jiankong',
          icon: 'el-icon-video-camera-solid'
        },
        {
          index: '/qingxu',
          name: '实时情绪',
          active: 'qingxu',
          icon: 'el-icon-timer'
        }
      ],
      editableTabsValue: '2',
      editableTabs: [{
        title: '学生管理',
        name: '1',
        content: '/student'
      }, {
        title: '教师管理',
        name: '2',
        content: '/teacher'
      }],
      tabIndex: 2,
      nowRoute: ''
    }
  },
  created () {
    if (window.sessionStorage.getItem('path') !== null) {
      this.activePath = window.sessionStorage.getItem('path')
      this.$router.push(this.activePath)
    } else {
      this.activePath = 'xitong'
    }
    if (window.sessionStorage.getItem('list') !== null) {
      // this.editableTabs = window.sessionStorage.getItem('list')
      // console.log(window.sessionStorage.getItem('list'))
      var list = window.sessionStorage.getItem('list').split(',')
      list.forEach((tab, index) => {
        if (tab !== '') {
          this.addTab(tab)
        }
      })
      // var jsonData = JSON.stringify(window.sessionStorage.getItem('list'))// 转成JSON格式
      // var result = $.parseJSON(jsonData)// 转成JSON对象
      // console.log(result)
    }
    if (window.sessionStorage.getItem('targetName') !== null) {
      // console.log(window.sessionStorage.getItem('targetName'))
      this.addTab(window.sessionStorage.getItem('targetName'))
    }
    if (window.sessionStorage.getItem('targetName2') !== null) {
      // console.log(window.sessionStorage.getItem('targetName2'))
      this.addTab(window.sessionStorage.getItem('targetName2'))
    }
  },
  methods: {
    logout () {
      this.$http.get('/logout')
      this.$message.success('退出成功')
      window.sessionStorage.clear()
      this.$router.push('/login')
    },
    toggleCollapse () {
      this.isCollapse = !this.isCollapse
      if (this.isCollapse === false) {
        $('.asideTop').css('display', 'block')
      } else {
        $('.asideTop').css('display', 'none')
      }
    },
    goto (where) {
      if (where !== this.nowRoute) {
        this.$router.push(where)
      }
      this.nowRoute = where
    },
    changePath (path) {
      $('.topmenu').removeClass('checkedMenu')
      $('#' + path).addClass('checkedMenu')
      window.sessionStorage.setItem('path', path)
      this.activePath = path
    },
    addTab (targetName) {
      if (this.findeTab(targetName) !== false) {
        this.removeTab(this.findeTab(targetName))
      }
      const newTabName = ++this.tabIndex + ''
      this.menuList.forEach((tab, index) => {
        if (targetName === tab.index) {
          this.editableTabs.push({
            title: tab.name,
            name: newTabName,
            content: tab.index
          })
          this.editableTabsValue = newTabName
          window.sessionStorage.setItem('targetName', targetName)
          this.saveTab()
          // console.log(this.saveTab())
        }
      })
    },
    removeTab (targetName) {
      var tabs = this.editableTabs
      var activeName = this.editableTabsValue
      if (activeName === targetName) {
        tabs.forEach((tab, index) => {
          if (tab.name === targetName) {
            const nextTab = tabs[index + 1] || tabs[index - 1]
            if (nextTab) {
              activeName = nextTab.name
            }
          }
        })
      }
      this.editableTabsValue = activeName
      this.editableTabs = tabs.filter(tab => tab.name !== targetName)
      window.sessionStorage.setItem('targetName', targetName)
      this.saveTab()
    },
    findeTab (targetName) {
      var flag = false
      var tabs = this.editableTabs
      tabs.forEach((tab, index) => {
        if (tab.content === targetName) {
          flag = tab.name
        }
      })
      return flag
    },
    saveTab () {
      var str = ''
      var tabs = this.editableTabs
      tabs.forEach((tab, index) => {
        str = str + tab.content + ','
      })
      window.sessionStorage.setItem('list', str)
    },
    change () {
      this.nowRoute = this.$route.path
      var tabs = this.editableTabs
      tabs.forEach((tab, index) => {
        if (tab.name === this.editableTabsValue) {
          this.goto(tab.content)
          window.sessionStorage.setItem('targetName2', tab.content)
          // console.log(window.sessionStorage.getItem('targetName'))
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
.home-container {
  height: 100%;
}
.topmenu{
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  width: 110px;
  height: 60px;
  line-height: 60px;
  font-weight: 600;
  margin-left: 5px;
  font-size: 17px;
  cursor: pointer;
  text-align: center;
}
.el-menu-vertical-demo{
  color: white;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-weight: 600;
  cursor: pointer;
}

.el-header {
  background-color: #3c8ebb;
  display: flex;
  justify-content: space-between;
  padding-left: 0;
  align-items: center;
  color: #fff;
  font-size: 20px;

  >div {
    display: flex;
    align-items: center;

  }
}

.el-aside {
  background-color: #0f2a50;

  .el-menu {
    border-right: none;
  }
}

.el-main {
  padding: 0;
  padding-left: 10px;
  padding-right: 10px;
  background-color: #eaedf1;
}

.iconfont {
  margin-right: 10px;
}

.toggle-button {
  background-color: #0f2a50;
  font-size: 10px;
  line-height: 24px;
  color: #fff;
  text-align: center;
  letter-spacing: 0.2em;
  cursor: pointer;
}

.logo {
  width: 200px;
  background-color: #3580a8;
  height: 60px;
  float: left;
  line-height: 60px;
  text-align: center;
}
.shou{
  width: 60px;
  text-align: center;
  cursor: pointer;
}
.checkedMenu{
  background-color: #3580a8;
}
.asideTop{
  width: 200px;
  height: 80px;
}
.asideTopLeft{
  width: 80px;
  height: 80px;
  text-align: center;
  float: left;
}
.touxiang{
  margin-top: 10px;
}
.asideTopRight{
  float: left;
  color: white;
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-weight: 600;
  font-size: 16px;
  width: 60px;
  height: 60px;
}
.zaixian{
  width: 10px;
  height: 10px;
  border-radius: 10px;
  background-color: green;
}
.zhuxiao{
  width: 35px;
  height: 40px;
  float:left;
  color: white;
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-weight: 600;
  font-size: 16px;
  p{
    margin-top: 53px;
    cursor: pointer;
  }
}
.user{
  color: white;
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-weight: 600;
}
</style>
