<template>
<div>
  <div class="top_sreach">
    <div class="top_zi">学生姓名:</div>
    <div class="search_input">
      <input type="text" class="input_box" v-model="studentForm.name" @blur="getStudent()">
    </div>
    <div class="top_zi">所在班级:</div>
    <!-- <div class="search_input">
      <input type="text" class="input_box" v-model="studentForm.classid" @blur="getStudent()">
    </div>
    <div class="top_zi">学生状态:</div> -->
    <div class="search_input" @blur="getStudent()">
      <el-select v-model="studentForm.classid" clearable filterable @blur="getStudent()">
      <el-option
        v-for="item in options"
        :key="item.id"
        :label="item.class_name"
        :value="item.id"
        @blur="getStudent()">
      </el-option>
    </el-select>
    </div>
    <div class="search_input">
      <el-button type="success" size="mini" round @click="getStudent()"><i class="el-icon-s-promotion"></i>搜索</el-button>
    </div>
    <div class="search_input">
      <el-button type="warning" size="mini" round @click="resetSearch()"><i class="el-icon-refresh"></i>重置</el-button>
    </div>
    <div class="search_input">
      <el-button type="warning" size="mini" round @click="showadd()"><i class="el-icon-refresh"></i>添加学生</el-button>
    </div>
  </div>
  <div class="top_main">
     <el-table
    :data="studentList"
    stripe
    style="width: 100%">
    <el-table-column
      prop="name"
      label="姓名"
      >
    </el-table-column>
    <el-table-column
      prop="sex"
      label="姓别"
      >
    </el-table-column>
    <el-table-column
      prop="class_id"
      label="班级"
      >
      <!-- <template slot-scope="scope">
      <span>{{ reversedMessage(scope.row.class_id) }}</span>
      </template> -->
    </el-table-column>
    <el-table-column
      prop="familyPhone"
      label="手机号">
    </el-table-column>
    <el-table-column
      prop="feeling"
      label="状态">
    </el-table-column>
    <el-table-column
      label="心情"
      width="300"
      >
      <template slot-scope="scope">
        <div v-for="item in scope.row.mood" :key="item">
          <el-button type="danger" size="mini" style="float:left;margin-left:10px" v-if="scope.row.feeling === '异常'">{{item}}</el-button>
          <el-button type="success" size="mini" style="float:left;margin-left:10px" v-if="scope.row.feeling === '正常'">{{item}}</el-button>
        </div>
      </template>
    </el-table-column>
    <el-table-column label="操作" width="180px">
    <template slot-scope="scope">
        <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.student_id)"></el-button>
        <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeStudentById(scope.row.student_id)"></el-button>
        <el-tooltip effect="dark" content="详情" placement="top" :enterable="false" >
        <el-button type="warning" icon="el-icon-setting" size="mini" @click="setRoles(scope.row)"></el-button>
    </el-tooltip>
              </template>
          </el-table-column>
  </el-table>
   <div class="block">
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="studentForm.pagenum"
      :page-sizes="[1, 2, 5, 10]"
      :page-size="studentForm.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
  </div>
  </div>
      <el-dialog
        title="添加学生"
        :visible.sync="addDialogVisible"
        width="50%"
        @close="addDialogClosed"
        >
        <!-- :rules="addFormRules" -->
        <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px" >
    <el-form-item label="姓名" prop="name">
    <el-input v-model="addForm.name"></el-input>
    </el-form-item>
    <el-form-item label="班级">
    <!-- <el-input v-model="addForm.class_id"></el-input> -->
    <el-select v-model="addForm.class_id">
      <el-option
        v-for="item in options"
        :key="item.id"
        :label="item.class_name"
        :value="item.id">
      </el-option>
    </el-select>
    </el-form-item>
    <el-form-item label="性别">
    <el-select v-model="addForm.sex">
      <el-option
        v-for="item in sexForm"
        :key="item.value"
        :label="item.label"
        :value="item.value">
      </el-option>
    </el-select>
    </el-form-item>
    <el-form-item label="手机" prop="familyPhone">
    <el-input v-model="addForm.familyPhone"></el-input>
    </el-form-item>
    <el-form-item label="住址" prop="homeaddress">
    <el-input v-model="addForm.homeaddress"></el-input>
    </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addStudent()">确 定</el-button>
  </span>
</el-dialog>
<el-dialog
  title="修改学生信息"
  :visible.sync="editDialogVisible"
  width="30%">
  <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px" @close="editDialogClosed" >
    <el-form-item label="姓名" prop="name">
    <el-input v-model="editForm.name"></el-input>
    </el-form-item>
    <el-form-item label="班级">
    <!-- <el-input v-model="editForm.class_id"></el-input> -->
    <el-select v-model="editForm.class_id">
      <el-option
        v-for="item in options"
        :key="item.id"
        :label="item.class_name"
        :value="item.id">
      </el-option>
    </el-select>
    </el-form-item>
    <el-form-item label="性别">
    <el-select v-model="editForm.sex">
      <el-option
        v-for="item in sexForm"
        :key="item.value"
        :label="item.label"
        :value="item.value">
      </el-option>
    </el-select>
    </el-form-item>
    <el-form-item label="手机" prop="familyPhone">
    <el-input v-model="editForm.familyPhone"></el-input>
    </el-form-item>
    <el-form-item label="住址" prop="homeaddress">
    <el-input v-model="editForm.homeaddress"></el-input>
    </el-form-item>
        </el-form>
  <span slot="footer" class="dialog-footer">
    <el-button @click="editDialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="editStudent()">确 定</el-button>
  </span>
</el-dialog>
</div>
</template>
<script>
export default {
  data () {
    return {
      options: [],
      studentForm: {
        name: '',
        classid: '',
        feeling: '',
        pagenum: 1,
        pagesize: 5
      },
      studentList: [],
      total: 0,
      addForm: {
        class_id: '',
        name: '',
        sex: '男',
        familyPhone: '',
        homeaddress: ''
      },
      addDialogVisible: false,
      addFormRules: {
        class_id: [{
          required: true,
          message: '请输入班级',
          trigger: 'blur'
        }],
        name: [{
          required: true,
          message: '请输入姓名',
          trigger: 'blur'
        }],
        sex: [{
          required: true,
          message: '请输入性别',
          trigger: 'blur'
        }],
        familyPhone: [{
          required: true,
          message: '请输入手机号',
          trigger: 'blur'
        }],
        homeaddress: [{
          required: true,
          message: '请输入家庭地址',
          trigger: 'blur'
        }]
      },
      sexForm: [
        {
          value: '男',
          label: '男',
          key: 0
        },
        {
          value: '女',
          label: '女',
          key: 1
        }
      ],
      editDialogVisible: false,
      editForm: {
        class_id: '',
        name: '',
        sex: '男',
        familyPhone: '',
        homeaddress: '',
        student_id: ''
      },
      editFormRules: {
        class_id: [{
          required: true,
          message: '请输入班级',
          trigger: 'blur'
        }],
        name: [{
          required: true,
          message: '请输入名字',
          trigger: 'blur'
        }],
        sex: [{
          required: true,
          message: '请输入性别',
          trigger: 'blur'
        }],
        familyPhone: [{
          required: true,
          message: '请输入手机号',
          trigger: 'blur'
        }],
        homeaddress: [{
          required: true,
          message: '请输入家庭地址',
          trigger: 'blur'
        }]
      }
    }
  },
  created () {
    this.getStudent()
    this.getClass()
  },
  methods: {
    async getStudent () {
      const { data: res } = await this.$http.get('student/classandname', { params: this.studentForm })
      if (res.meta.status !== 200) {
        return this.$message.error('获取失败')
      }
      if (res.meta.msg === '无查询到数据') {
        this.studentList = []
        this.total = 0
        return this.$message.error('无查询到数据')
      }
      this.studentList = res.data.records
      this.total = res.data.total
    },
    async getClass () {
      const { data: res } = await this.$http.get('getallclass')
      if (res.meta.status !== 200) {
        return this.$message.error('获取失败')
      }
      this.options = res.data
    },
    handleSizeChange (newSize) {
      this.studentForm.pagesize = newSize
      this.getStudent()
    },
    handleCurrentChange (newPage) {
      this.studentForm.pagenum = newPage
      this.getStudent()
    },
    resetSearch () {
      this.studentForm.name = ''
      this.studentForm.classid = ''
      this.studentForm.name = ''
      this.studentForm.classid = ''
      this.getStudent()
    },
    addDialogClosed () {
      this.$refs.addFormRef.resetFields()
    },
    editDialogClosed () {
      this.$refs.editFormRef.resetFields()
    },
    showadd () {
      this.addDialogVisible = true
    },
    addStudent () {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return 0
        const { data: res } = await this.$http.post('addstudent', this.addForm)
        if (res.meta.status !== 201) {
          return this.$message.error('添加学生失败')
        }
        this.$message.success('添加学生成功')
        this.addDialogVisible = false
        this.getStudent()
      })
    },
    async removeStudentById (id) {
      const confirmResult = await this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => {
        return err
      })
      if (confirmResult !== 'confirm') {
        return this.$message.info('已取消删除')
      }
      const { data: res } = await this.$http.delete('deletestudent/' + id)
      if (res.meta.status !== 200) return this.$message.error('删除用户失败')
      this.$message.success('删除用户成功')
      this.getStudent()
    },
    async showEditDialog (id) {
      const { data: res } = await this.$http.get('student/' + id)
      if (res.meta.status !== 200) return this.$message.error('查询失败')
      this.editForm = res.data
      this.editDialogVisible = true
    },
    editStudent () {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return 0
        const { data: res } = await this.$http.put('updateStudent/' + this.editForm.student_id, this.editForm)
        if (res.meta.status !== 200) {
          return this.$message.error('修改学生失败')
        }
        this.$message.success('修改学生成功')
        this.editDialogVisible = false
        this.getStudent()
      })
    }
  }
}
</script>
<style lang="less" scoped>
.top_sreach{
  width: 100%;
  height: 60px;
  background-color: white;
  border-radius: 10px;
}
.search_input{
  line-height: 60px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 15px;
  float: left;
  margin-left: 10px;
}
.top_zi{
  line-height: 60px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 15px;
  margin-left: 20px;
  float: left;
  color: #6b6b6b;
}
.input_box{
  width: 180px;
  height: 25px;
  border-radius: 5px;
  border: 1px solid #b6b6b6;
}
.top_main{
  width: 100%;
  margin-top: 10px;
  background-color: white;
  border-radius: 10px;
}
</style>
