<template>
<div class="loginForm">
  <div class="logo">
    LOGO
  </div>
  <div class="text">
    真正的管理人是去管理人的情绪。
  </div>
  <div class="form">
    <el-form ref="registerFormRef" :model="registerForm" :rules="registerFormRules" label-width="0px">
      <el-form-item prop="username" class="ceshi">
        <el-input v-model="registerForm.username" class="username" placeholder="账号"></el-input>
        <!-- <input type="username" placeholder="   账号" class="username"> -->
      </el-form-item>
      <el-form-item prop="password">
        <el-input v-model="registerForm.password" class="password" type="password" placeholder="密码"></el-input>
        <!-- <input type="password" placeholder="   密码" class="password"> -->
      </el-form-item>
      <el-form-item prop="twicePassword">
        <el-input v-model="registerForm.twicePassword" class="twicePassword" type="password" placeholder="确认密码"></el-input>
        <!-- <input type="password" placeholder="   密码" class="password"> -->
      </el-form-item>
      <el-form-item prop="teacher_id">
        <el-input v-model="registerForm.teacher_id" class="twicePassword" placeholder="教师工号"></el-input>
        <!-- <input type="password" placeholder="   密码" class="password"> -->
      </el-form-item>
      <el-form-item prop="teacher_phone">
        <el-input v-model="registerForm.teacher_phone" class="twicePassword" placeholder="手机号"></el-input>
        <!-- <input type="password" placeholder="   密码" class="password"> -->
      </el-form-item>
    </el-form>
    <!-- <input type="text" placeholder="   账号" class="username" id="username" v-model="registerForm.username" @blur="checkUsername()"> -->
    <!-- <input type="password" placeholder="   密码" class="password" v-model="registerForm.password"> -->
    <!-- <input type="password" placeholder="   确认密码" class="password" v-model="registerForm.twicePassword"> -->
    <!-- <input type="password" placeholder="   教师工号" class="password" v-model="registerForm.teacher_id"> -->
    <!-- <input type="password" placeholder="   手机号" class="password" v-model="registerForm.teacher_phone"> -->
    <div class="di">
      <el-input v-model="registerForm.yanzhenma" placeholder="验证码" class="yanzhenma"></el-input>
      <!-- <input type="password" placeholder="   验证码" class="yanzhenma"> -->
      <el-button type="primary" class="uploadYan">获取验证码</el-button>
    </div>
  </div>
  <el-button type="primary" class="login" @click="register()">注册</el-button>
  <div class="Login_botton">
    <div class="Login_botton_left"><a href="#" @click="goback()">返回登录</a></div>
    <div class="Login_botton_right"><a href="#">忘记密码</a></div>
  </div>
  <div class="check">
    <el-checkbox v-model="checked">我已阅读相关<a href="#">服务条款</a>和<a href="#">隐私政策</a></el-checkbox>
  </div>
</div>
</template>

<script>
export default {
  data () {
    return {
      registerForm: {
        username: '',
        password: '',
        twicePassword: '',
        teacher_id: '',
        teacher_phone: '',
        yanzhenma: ''
      },
      isOK: true,
      checked: false,
      registerFormRules: {
        username: [{
          required: true,
          message: '请输入登录名称',
          trigger: 'blur'
        },
        {
          min: 3,
          max: 10,
          message: '长度在3到10个字符',
          trigger: 'blur'
        }
        ],
        password: [{
          required: true,
          message: '请输入登录密码',
          trigger: 'blur'
        },
        {
          min: 6,
          max: 15,
          message: '长度在6到15个字符',
          trigger: 'blur'
        }
        ],
        twicePassword: [{
          required: true,
          message: '请输入登录密码',
          trigger: 'blur'
        },
        {
          min: 6,
          max: 15,
          message: '长度在6到15个字符',
          trigger: 'blur'
        }
        ],
        teacher_id: [{
          required: true,
          message: '请输入教师工号',
          trigger: 'blur'
        },
        {
          min: 1,
          max: 15,
          message: '长度在1到15个字符',
          trigger: 'blur'
        }
        ],
        teacher_phone: [{
          required: true,
          message: '请输入手机号',
          trigger: 'blur'
        },
        {
          message: '请输入正确的手机号',
          trigger: 'blur',
          pattern: /^1(3\d|4[5-9]|5[0-35-9]|6[567]|7[0-8]|8\d|9[0-35-9])\d{8}$/
        }
        ]
      }
    }
  },
  methods: {
    resetRegisterForm () {
      this.$refs.registerFormRef.resetFields()
    },
    register () {
      this.$refs.registerFormRef.validate(async (valid) => {
        if (!valid) {
          return 0
        }
        if (this.checked === false) {
          return this.$message.error('请选中协议')
        }
        if (this.registerForm.password !== this.registerForm.twicePassword) {
          this.registerForm.password = ''
          this.registerForm.twicePassword = ''
          return this.$message.error('两次输入的密码不一致')
        }
        const { data: res } = await this.$http.post('login', this.loginForm)
        if (res.meta.status !== 200) return this.$message.error('登录失败')
        this.$message.success('登录成功')
        window.sessionStorage.setItem('token', res.data.token)
        this.$router.push('/home')
      })
    },
    goback () {
      this.$router.push('/login')
    }
  }
}
</script>

