      font-weight: 600;
      border: none;
      border-radius: 10px 0px 10px 10px;
      background-color: #c0c3c5;
      color: #8a8d8f;
    }

    .di {
      background-color: white;
      height: 54px;
      width: 300px;
      margin-left: 50px;
      border-radius: 0px 0px 10px 10px;
      border-top: 2px solid rgba(63, 58, 58, 0.432);
    }
  }

  .login {
    width: 300px;
    height: 60px;
    margin-left: 50px;
    background-color: #9786ba9d;
    font-size: 20px;
    color: black;
    border: none;
  }

  .Login_botton {
    width: 300px;
    height: 50px;
    margin-left: 50px;
    font-size: 20px;
    margin-top: 10px;
    font-weight: 600;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

    .Login_botton_left {
      float: left;

      a {
        color: #000;
        text-decoration: none;
      }
    }

    .Login_botton_right {
      float: right;

      a {
        color: #000;
        text-decoration: none;
      }
    }
  }

  .check {
    width: 300px;
    height: 30px;
    margin-left: 50px;

    .el-checkbox {
      color: #000;

      a {
        color: blue;
      }
    }
  }
}
</style>
