<template>
<div>
  <div class="top_sreach">
    <div class="top_zi">学生姓名:</div>
    <div class="search_input">
      <input type="text" class="input_box" v-model="classForm.name">
    </div>
    <div class="top_zi">所在班级:</div>
    <div class="search_input">
      <input type="text" class="input_box" v-model="classForm.grade">
    </div>
    <div class="search_input">
      <el-button type="success" round><i class="el-icon-s-promotion"></i>搜索</el-button>
    </div>
    <div class="search_input">
      <el-button type="warning" round><i class="el-icon-refresh"></i>重置</el-button>
    </div>
  </div>
  <div class="top_main">
    <el-table
    :data="classList"
    stripe
    style="width: 100%">
    <el-table-column
      prop="class_name"
      label="姓名"
      >
    </el-table-column>
    <el-table-column
      prop="grade"
      label="年级"
      >
    </el-table-column>
    <el-table-column label="操作" width="180px">
    <template slot-scope="scope">
        <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.student_id)"></el-button>
        <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeStudentById(scope.row.student_id)"></el-button>
        <el-tooltip effect="dark" content="详情" placement="top" :enterable="false" >
        <el-button type="warning" icon="el-icon-setting" size="mini" @click="setRoles(scope.row)"></el-button>
    </el-tooltip>
              </template>
          </el-table-column>
  </el-table>
  </div>
</div>
</template>
<script>
export default {
  data () {
    return {
      classForm: {
        name: '',
        class_id: '',
        feeling: '',
        pagenum: '',
        pagesize: ''
      },
      classList: []
    }
  },
  created () {
    this.getClass()
  },
  methods: {
    async getClass () {
      const { data: res } = await this.$http.get('getallclass')
      if (res.meta.status !== 200) {
        return this.$message.error('获取失败')
      }
      if (res.meta.msg === '无查询到数据') {
        this.classList = []
        this.total = 0
        return this.$message.error('无查询到数据')
      }
      this.classList = res.data
      // this.total = res.data.total
    }
  }
}
</script>
<style lang="less" scoped>
.top_sreach{
  width: 100%;
  height: 60px;
  background-color: white;
  border-radius: 10px;
}
.search_input{
  line-height: 60px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 15px;
  float: left;
  margin-left: 10px;
}
.top_zi{
  line-height: 60px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 15px;
  margin-left: 20px;
  float: left;
  color: #6b6b6b;
}
.input_box{
  width: 180px;
  height: 25px;
  border-radius: 5px;
  border: 1px solid #b6b6b6;
}
.top_main{
  width: 100%;
  margin-top: 10px;
  background-color: white;
  border-radius: 10px;
}
</style>
