<template>
<div class="loginForm">
  <div class="logo">
    LOGO
  </div>
  <div class="text">
    真正的管理人是去管理人的情绪。
  </div>
  <div class="form">
    <el-form ref="loginFormRef" :model="loginForm" :rules="loginFormRules" label-width="0px" class="login_form">
      <el-form-item prop="username" class="ceshi">
        <el-input v-model="loginForm.username" class="username" placeholder="账号"></el-input>
        <!-- <input type="username" placeholder="   账号" class="username"> -->
      </el-form-item>
      <el-form-item prop="password">
        <el-input v-model="loginForm.password" class="password" type="password" placeholder="密码"></el-input>
        <!-- <input type="password" placeholder="   密码" class="password"> -->
      </el-form-item>
    </el-form>
  </div>
  <el-button type="primary" class="login" @click="login()">登入</el-button>
  <div class="Login_botton">
    <div class="Login_botton_left" @click="gotoRegister()"><a href="#">注册账号</a></div>
    <div class="Login_botton_right"><a href="#">忘记密码</a></div>
  </div>
  <div class="check">
    <el-checkbox v-model="checked">我已阅读相关<a href="#">服务条款</a>和<a href="#">隐私政策</a></el-checkbox>
  </div>
</div>
</template>

<script>
export default {
  data () {
    return {
      loginForm: {
        username: '',
        password: ''
      },
      loginFormRules: {
        username: [{
          required: true,
          message: '请输入登录名称',
          trigger: 'blur'
        },
        {
          min: 3,
          max: 10,
          message: '长度在3到10个字符',
          trigger: 'blur'
        }
        ],
        password: [{
          required: true,
          message: '请输入登录密码',
          trigger: 'blur'
        },
        {
          min: 6,
          max: 15,
          message: '长度在6到15个字符',
          trigger: 'blur'
        }
        ]
      },
      checked: false
    }
  },
  methods: {
    resetLoginForm () {
      this.$refs.loginFormRef.resetFields()
    },
    login () {
      this.$refs.loginFormRef.validate(async (valid) => {
        if (!valid) {
          return 0
        }
        if (this.checked === false) {
          return this.$message.error('请选中协议')
        }
        const { data: res } = await this.$http.post('login?username=' + this.loginForm.username + '&password=' + this.loginForm.password)
        if (res.meta.status !== 200) return this.$message.error('用户名或密码错误')
        this.$message.success('登录成功')
        window.sessionStorage.setItem('token', res.data.token)
        this.$router.push('/home')
      })
    },
    gotoRegister () {
      this.$router.push('/register')
    }
  }
}
</script>

<style lang="less" scoped>
.loginForm {
  width: 400px;
  height: 500px;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 20px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);

  .logo {
    width: 400px;
    height: 70px;
    margin-top: 50px;
    font-size: 70px;
    text-align: center;
    font-weight: 900;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  }

  .text {
    width: 400px;
    height: 50px;
    text-align: center;
    font-size: 20px;
    line-height: 50px;
    font-weight: 700;
  }

  .form {
    margin-top: 30px;
    width: 400px;
    height: 130px;
    opacity: 0.7;

    .username {
      width: 300px;
      height: 50px;
      font-size: 20px;
      font-weight: 600;
      margin-left: 50px;
      border-radius: 10px 10px 0px 0px;
      border: none;
    }

    .password {
      width: 300px;
      height: 50px;
      font-size: 20px;
      font-weight: 600;
      margin-left: 50px;
      border-radius: 0px 0px 10px 10px;
      border: none;
      border-top: 2px solid rgba(63, 58, 58, 0.432);
    }
  }

  .login {
    width: 300px;
    height: 60px;
    margin-left: 50px;
    background-color: #9786ba9d;
    font-size: 20px;
    color: black;
    border: none;
  }

  .Login_botton {
    width: 300px;
    height: 50px;
    margin-left: 50px;
    font-size: 20px;
    margin-top: 10px;
    font-weight: 600;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

    .Login_botton_left {
      float: left;

      a {
        color: #000;
        text-decoration: none;
      }
    }

    .Login_botton_right {
      float: right;

      a {
        color: #000;
        text-decoration: none;
      }
    }
  }

  .check {
    width: 300px;
    height: 30px;
    margin-left: 50px;

    .el-checkbox {
      color: #000;

      a {
        color: blue;
      }
    }
  }
}
</style>
<style scoped>
.username>>>.el-input__inner{
  width: 300px!important;
  height: 50px!important;
  font-size: 20px!important;
  font-weight: 600!important;
  border-radius: 10px 10px 0px 0px!important;
  border: none!important;
}
.password>>>.el-input__inner{
  width: 300px;
  height: 50px;
  font-size: 20px;
  font-weight: 600;
  border-radius: 0px 0px 10px 10px;
  border: none;
  margin-top: -40px;
  border-top: 2px solid rgba(63, 58, 58, 0.432);
}
.el-form-item{
  margin: 0;
}
.el-form-item>>> .el-form-item__error {
  color: #F56C6C;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 1;
  padding-top: 4px;
  position: absolute;
  top: 5px;
  width: 150px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  background-color: white;
  border-radius: 10px;
  left: 375px;
}
</style>
