import Vue from 'vue'
import Router from 'vue-router'
import LoginHome from '../components/LoginHome.vue'
import Home from '../components/Home.vue'
import Welcome from '../components/Welcome.vue'
import Login from '../components/Login/Login.vue'
import Register from '../components/Login/Register.vue'
import test from '../components/Home/test.vue'
import ProblemStudent from '../components/Home/problemStudent.vue'
import PsyHealth from '../components/Home/psyHealth.vue'
import SolveReport from '../components/Home/solveReport.vue'
import Student from '../components/Home/student.vue'
import Teacher from '../components/Home/teacher.vue'
import Jiankong from '../components/Home/jiankong.vue'
import Qingxu from '../components/Home/qingxu.vue'
import Class from '../components/Home/class.vue'

Vue.use(Router)

const router = new Router({
  routes: [
    { path: '/', redirect: '/loginHome' },
    {
      path: '/loginHome',
      component: LoginHome,
      redirect: '/login',
      children: [
        { path: '/login', component: Login },
        { path: '/register', component: Register }
      ]
    },
    {
      path: '/home',
      component: Home,
      redirect: '/welcome',
      children: [
        { path: '/welcome', component: Welcome },
        { path: '/test', component: test },
        { path: '/problemStudent', component: ProblemStudent },
        { path: '/psyHealth', component: PsyHealth },
        { path: '/solveReport', component: SolveReport },
        { path: '/student', component: Student },
        { path: '/teacher', component: Teacher },
        { path: '/jiankong', component: Jiankong },
        { path: '/qingxu', component: Qingxu },
        { path: '/class', component: Class }
      ]
    }
  ]
})

router.beforeEach((to, from, next) => {
  if (to.path === '/login' || to.path === '/register') return next()
  const tokenStr = window.sessionStorage.getItem('token')
  if (!tokenStr) return next('/login')
  next()
})

export default router
